@import url(/assets/css/freelancer.min.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.bg-dark-section-1 {
  background-color: #313131 !important; }

.bg-dark-section-2 {
  background-color: #c3c3c3 !important; }

.star-danger {
  background: #ff0c0c !important; }

.h-100-px {
  height: 100px !important; }

.bg-blue-lv1 {
  background: #337AB7 !important; }

.bg-blue-lv2 {
  background: #BFE5FB !important; }

.bg-blue-lv3 {
  background-color: #D2D2FF !important; }

.bg-gray-lv1 {
  background: #E2E2E0 !important; }

.bg-black {
  background: #000; }

.font-12-px {
  font-size: 12px; }

.font-13-px {
  font-size: 13px; }

.table-home {
  font-size: 12px; }
  .table-home thead th {
    padding: 5px !important; }
  .table-home tbody {
    font-weight: bold; }
    .table-home tbody td {
      padding: 5px !important; }
    .table-home tbody .list-group-item {
      background: #E2E2E0;
      padding: 5px !important; }

.Header nav {
  height: 46px; }

.Side {
  height: 100vh;
  /* Change the link color on hover */ }
  .Side ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 200px;
    background-color: #f1f1f1; }
  .Side li a {
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none; }
  .Side li a:hover {
    background-color: #285f8f;
    color: white; }
  .Side .img-side-menu {
    width: 23px;
    height: 22px; }

body {
  height: 100%; }

.SignIn,
.SignUp {
  background-image: url("/assets/img/background_login_2.png");
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  height: 100%;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }
  .SignIn input[type=text],
  .SignIn .SignIn select,
  .SignUp input[type=text],
  .SignUp .SignIn select {
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    background-color: #f8f9fa !important; }
  .SignIn label,
  .SignUp label {
    font-weight: bold; }
  .SignIn img.logo-login,
  .SignUp img.logo-login {
    margin: 50px 0px 50px 0px; }

.h-fixed {
  height: 865px; }

